<template>
  <div>
    <div>Quiz</div>
    <div class="progress">
      <div
        id="progressBar"
        class="progress-bar progress-bar-striped progress-bar-animated"
        role="progressbar"
        :aria-valuenow="progress"
        aria-valuemin="0"
        aria-valuemax="100"
        v-bind:style="{ width: 0 + 'progress' + '%' }"
      ></div>
    </div>
    <div class="container">
      <div class="row">
        <div class="col-sm-9">
          {{ quiz.question }}
          <div class="row" v-if="quiz.answer_cnt == 2">
            <div
              class="col-8 col-sm-6 alert alert-dark"
              role="alert"
              id="quizAns"
            >
              {{ quiz.ans1 }}
            </div>
            &nbsp;
            <div
              class="col-4 col-sm-6 alert alert-dark"
              role="alert"
              id="quizAns"
            >
              {{ quiz.ans2 }}
            </div>
          </div>
          <div class="row" v-if="quiz.answer_cnt == 3">
            <div
              class="col-8 col-sm-6 alert alert-dark"
              role="alert"
              id="quizAns"
            >
              {{ quiz.ans1 }}
            </div>
            &nbsp;
            <div
              class="col-4 col-sm-6 alert alert-dark"
              role="alert"
              id="quizAns"
            >
              {{ quiz.ans2 }}
            </div>
            &nbsp;
            <div
              class="col-2 col-sm-6 alert alert-dark"
              role="alert"
              id="quizAns"
            >
              {{ quiz.ans3 }}
            </div>
          </div>
          <div class="row" v-if="quiz.answer_cnt == 4">
            <div
              class="col-8 col-sm-6 alert alert-dark"
              role="alert"
              id="quizAns"
            >
              {{ quiz.ans1 }}
            </div>
            &nbsp;
            <div
              class="col-4 col-sm-6 alert alert-dark"
              role="alert"
              id="quizAns"
            >
              {{ quiz.ans2 }}
            </div>
          </div>
          <div class="row" v-if="quiz.answer_cnt == 4">
            <div
              class="cl-8 col-sm-6 alert alert-dark"
              role="alert"
              id="quizAns"
            >
              {{ quiz.ans3 }}
            </div>
            &nbsp;
            <div
              class="col-4 col-sm-6 alert alert-dark"
              role="alert"
              id="quizAns"
            >
              {{ quiz.ans4 }}
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import Constant from "../Constant";

export default {
  name: "quiz",
  created() {
    this.$store.dispatch(Constant.GET_QUIZ_ONE, {
      level: this.$store.state.level
    });
    this.getId();
    setInterval(function() {
      this.progress += 1;
    }, 100);
  },
  computed: {
    quiz() {
      return this.$store.state.quiz;
    }
  },
  data() {
    return {
      progress: 0
    };
  },
  methods: {
    GoQuizMain() {
      this.$router.push("/");
    },
    getId() {
      this.$store.dispatch(Constant.GET_ID);
    }
  }
};
</script>

<style scoped></style>
